<template>
<div class="nft-bag flex flex-column page container-full">
  <header>
    <h1 class="text">{{ language.tip_3_1 }}</h1>
    <p>{{ language.tip_3_2 }}</p>
    <div class="filter-bar flex jc-end">
<!--      <div class="sort-box flex jc-between ai-center text">-->
<!--        <span>{{ language.text_3_2 }}</span>-->
<!--        <img class="icon-s" src="../../images/option/order1.png" alt="">-->
<!--      </div>-->
      <label class="search-box">
        <input type="number" v-model="numText" :placeholder="language.tip_3_3">
      </label>
    </div>
  </header>
  <ul class="bag flex1">
    <li class="card" v-for="myCard in myCardsFiltered"
        @click="showCardDetail(myCard)"
        :key="myCard.id">
      <img src="../../images/lottery/card-default.png" alt="">
      <img class="url-img" :src="myCard.resUrl" alt="" onload="">
      <p class="flex jc-between ai-center text fs-12">
        <span class="no">#{{ myCard.id }}</span>
        <span class="time">{{ myCard.createdTime }}</span>
      </p>
    </li>
    <li v-if="myCardsFiltered.length===0&&!bagLoading" class="no-data flex jc-center ai-center">
      <p class="text fs-16 ta-center">
        <img src="../../images/no-data.png" alt="nodata"><br>
        {{language.nodata}} NFT
      </p>
    </li>
    <li v-if="bagLoading" class="mask flex jc-center ai-center">
      <i class="icon-loading"></i>
    </li>
  </ul>
<!--  卡牌详情弹层-->
  <m-popper v-model="detailShow">
    <template v-slot:title>{{ language.tt_4_4 }}</template>
    <div class="card-detail">
      <img class="card-img" :src="nftDetailInfo.resUrl" alt="">
      <div class="r-box">
        <h1 class="name">NFT #{{ nftDetailInfo.id }}</h1>
        <p>{{ language.label_lv }}：<b class="lv">Lv.{{ nftDetailInfo.grade }}</b></p>
        <!--          <p>{{ language.label_attr }}</p>-->
        <p class="p1 flex">
          <span>{{ language.label_2_1 }}<br>{{ nftDetailInfo.createdTime }}</span>
          <span class="num">{{ language.label_2_2 }}<br>{{ nftDetailInfo.amount }}</span>
        </p>
        <p><span>{{ language.label_2_3 }}<br>{{ nftDetailInfo.blockNum }}</span></p>
        <p><span>{{ language.label_2_4 }}<br>{{ nftDetailInfo.author }}</span></p>
      </div>
      <div class="give-row flex jc-end ai-center">
        <label><input type="text" v-model="giveAddress" :placeholder="language.tip_2_4"></label>
        <m-button class="btn hdr-el-blue" @click="give()" :loading="giveNftLoading">
          <span>{{ language.btn_give_card }}</span>
        </m-button>
      </div>
    </div>
  </m-popper>
</div>
</template>

<script>
import {mapActions, mapGetters, mapState} from "vuex";
import MPopper from "@/components/mPopper";
import MButton from "@/components/mButton";
import {BN} from "@/common/util";

export default {
  name: "nftBag",
  components: {MButton, MPopper},
  computed:{
    ...mapState('app',['address']),
    ...mapState('nft',['myCards']),
    ...mapGetters(['language']),
    myCardsFiltered(){
      return this.myCards.filter(c=>c.id.indexOf(this.numText)!==-1)
    },
  },
  watch:{
    address(){
      this.getCardList();
    }
  },
  data() {
    return {
      numText:'',
      detailShow: false,
      nftDetailInfo: {
        amount: new BN(0.01).myFixed('balance'),
        author: "0x1ca8e489197d06414cf5f311306e1e90e831b288",
        blockNum: "8807541",
        createdTime: "1624349563",
        grade: "1",
        id: "7",
        resUrl: "https://honcur-test.oss-cn-beijing.aliyuncs.com/prediction/7"
      }, //当前卡牌详情
      giveAddress: '',
      recordLoading: false,
      bagLoading: false,
      giveNftLoading: false,
    }
  },
  methods: {
    ...mapActions('nft',['setMyCards', 'deleteCard']),
    getCardList() {
      if (this.bagLoading) return;
      this.bagLoading = true;
      this.$api.gqlBagNftList(this.address).then(()=>{
        this.bagLoading = false;
      }).catch(()=>{
        this.bagLoading = false;
      });
    },
    setRecordLoading(bool = true) {
      this.recordLoading = bool;
    },
    showCardDetail(info) {
      this.nftDetailInfo = info;
      this.detailShow = true;
    },
    give() {
      let {address, giveAddress, nftDetailInfo} = this;
      if (!giveAddress || !nftDetailInfo.id) return;
      this.giveNftLoading = true;
      this.$api.giveNft(address, giveAddress.toLowerCase(), nftDetailInfo.id).then(() => {
        this.deleteCard([nftDetailInfo.id]);
        this.giveNftLoading = false;
        this.detailShow = false;
      }).catch(() => {
        this.giveNftLoading = false;
      })
    }
  },
  mounted() {
    this.getCardList();
  }
}
</script>

<style lang="less" scoped>
.nft-bag{

}
header{
  height: 190px;
  margin: 20px 0;
  padding: 20px 50px 10px;
  background-color: #191f2d;
  border-radius: 20px;
  box-sizing: border-box;
  h1{
    height: 38px;
    margin: 0 0 15px;
  }
  p{
    height: 65px;
  }
  .sort-box{
    width: 130px;
    height: 40px;
    padding: 0 10px 0 20px;
    border-radius: 20px;
    background-color: #353B4D;
    cursor: default;
    img{
      transform: rotate(180deg);
    }
  }
  .search-box{
    background-color: #353B4D;
    margin-left: 25px;
    padding: 0 20px;
    border-radius: 20px;
    input{
      width: 160px;
      height: 40px;
    }
  }
}
.bag {
  margin: 0 -1.5%;
  overflow-y: auto;
  position: relative;
  .mask{
    top: 0;
    left: 1.5%;
    right: 1.5%;
    border-radius: 10px;
  }
}
.card {
  width: 17%;
  margin: 1.5%;
  background-color: #191f2d;
  float: left;
  border-radius: 10px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  &:hover {
    box-shadow: 0 0 6px 2px rgba(0, 225, 255, 1), inset 0 0 6px 2px rgba(0, 225, 255, 1);
  }
  img {
    width: 100%;
    background-color: inherit;
    display: block;
    &.url-img{
      position: absolute;
      top: 0;
    }
  }
  p{
    width: 100%;
    height: 40px;
    padding: 0 5%;
    background-color: inherit;
  }
}
.no-data{
  height: 100%;
  line-height: 1.8;
}
.card-detail {
  padding: 10px 0 30px;
  width: 600px;
  color: #FFFFFF;
  font-size: 13px;
  img {
    width: 180px;
    float: left;
    border-radius: 10px;
  }
  .r-box {
    margin-left: 200px;
    h1 {
      margin: 0 0 10px;
    }
    p {
      margin-top: 10px;
      line-height: 2;
      .lv {
        font-size: 16px;
      }
      .num {
        display: inline-block;
        margin-left: 100px;
      }
    }
  }
  .give-row {
    margin-top: 30px;
    label {
      display: block;
      margin-right: 20px;
      background-color: #2E323E;
      border-radius: 20px;
      input {
        width: 480px;
        height: 40px;
        padding: 0 20px;
      }
    }
    .btn{
      width: 100px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .nft-bag{
    padding: 0 20px 20px;
  }
  header{
    margin: 10px 0;
    padding: 10px 15px;
    height: auto;
    h1{
      height: auto;
      font-size: 18px;
    }
    p{
      height: auto;
      font-size: 12px;
      margin-bottom: 15px;
    }
  }
  .tt{
    padding: 10px 15px;
  }
  .bag{
    margin: 0 -1%;
  }
  .card {
    width: 18%;
    margin: 1%;
  }
}
@media screen and (max-width: 768px) {
  .card {
    width: 23%;
    margin: 1%;
  }
}
@media screen and (max-width: 540px) {
  .nft-bag{
    padding: 0 15px;
  }
  .card {
    width: 47%;
    margin: 1.5%;
  }
  .card-detail {
    width: 70vw;
    font-size: 12px;
    img {
      width: 33%;
      margin-right: 15px;
      float: left;
    }
    .r-box {
      margin-left: 0;
      p {
        .num {
          margin-left: 0;
        }
      }
      .p1{
        justify-content: space-between;
      }
    }
    .give-row {
      margin-top: 10px;
      flex-wrap: wrap;
      justify-content: center;
      label {
        width: 100%;
        margin-right: 0;
        margin-bottom: 15px;
        input {
          width: 100%;
          height: 30px;
          padding: 0 20px;
        }
      }
    }
  }
}
::-webkit-scrollbar {
  display: none;
}
</style>